import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { EmployeeItem, ReposItem } from '../models/employee'

@Injectable()
export class EmployeeProvider {
  accessToken: string = '6db7e808b9b0e2e041da2b1d98d1d1ad27270c10';
  accessTokenString: string = '?access_token=' + this.accessToken;
  githubURL: string = 'https://api.github.com/orgs/codecentric/members';
  requestURL: string = this.githubURL + this.accessTokenString;

  constructor(private httpClient: HttpClient) {}

  getEmployeeList():Promise<any> {
    return new Promise(resolve => {
      this.httpClient.get(this.requestURL).subscribe((employeeList: EmployeeItem[]) => {
        employeeList.map(employee => {
          this.httpClient.get(employee.repos_url + this.accessTokenString).subscribe((employeeRepos: ReposItem[]) => {
            employee.employeeRepos = employeeRepos;
            employee.skills = this.getEmployeeSkills(employeeRepos);
          });

          Promise.all(employeeList).then(val => {
            resolve(val);
          })
        });
      });
    })
  }

  getEmployeeSkills(employeeRepos) {
    return employeeRepos.reduce((arr, curr) => {
      if(arr.indexOf(curr.language) === -1 && curr.language !== null)
      arr.push(curr.language);
      return arr;
    }, []);
  }
}



